import { makeStyles } from "@material-ui/core";

export const feature_style = makeStyles((theme) => ({
  route_inter: {
    position: "absolute",
    opacity: 0,

    transform: "scale(0)",
  },
  route_inter_active: {
    position: "absolute",
    opacity: 1,
    transition: "500ms",
    transform: "scale(1)",
  },

  route_exit: {
    position: "absolute",
    opacity: 1,
    transform: "scale(1)",
  },
  route_exit_active: {
    position: "absolute",
    opacity: 0,
    transition: "500ms",
    transform: "scale(0)",
  },
}));
